// 引入组件
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
import { BTN_TEXT, STATUS_CODE, TITLE } from '@/utils/constantsFile';
import detailTable from '@/views/report/MttrAndMtbf/detailTable.vue';
import chartModule from '@/views/report/MttrAndMtbf/chartModule.vue';
import { showWarningMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
import SearchBox from '@/components/common/SearchBox';

// 引入通用接口
import { deviceGroupGetTreeFunc } from '@/utils/publicFunction';
import { deviceGetPageDeviceComplexByConditionAction } from '@/api/management';
import { configurationGetConfigurationByEnum } from '@/api/system.js';
// import { getPageDeviceByCondition } from '../../../../api/management';
import { getDeviceExceptionComparedAnalyzeNew } from '@/api/andon.js';
// 导入通用方法
import {
  getStartTime,
  getEndTime
} from '@/utils/publicFunction2';
export default {
  name: 'EquipmentMonitoring',
  data() {
    return {
      // tab默认激活下标
      BTN_TEXT: BTN_TEXT,
      searchData: {
        MTBFVal: '850',
        MTTRVal: '120',
        dateVal: this.$utils.toDateString(new Date(), 'yyyy-MM-dd'),
        dateVal2: [],
        shiftVal: '',
        statisticsType: 1,
        dateType: 'day'
      },
      shiftList: [],
      typeOption: [
        {
          name: '产线',
          value: 1
        },
        {
          name: '设备',
          value: 0
        }
      ],
      nowOption: [],
      searchSelectList: [
        { value: [] }
      ],
      detailListName: [''],
      detailListx: ['测试'],
      activeN: 0,
      selectTabs: [
        {
          type: 0,
          name: '图表'
        },
        {
          type: 1,
          name: '列表'
        }
      ],
      loading: false,
      allData: [],
      pickerOptions: {
        shortcuts: [{
          text: '最近一周',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '最近一个月',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '最近三个月',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
            picker.$emit('pick', [start, end]);
          }
        }]
      }
    };
  },
  created() {
    this.searchData.dateVal2 = [getStartTime(), getEndTime()];

    // 调用列表查询接口
    // this.getWarnCount();
    this.getGroupTree();
    this.getShiftData();
  },
  methods: {
    toggleN(i) {
      this.activeN = i;
      this.search();
    },
    getValue3(e) {
      this.searchSelectList = [
        { value: null }
      ];
      this.nowOption = [];
      if (e === 0) {
        this.getDev();
      } else {
        this.getGroupTree();
      }
    },
    // getValue(e, index) {
    //   this.searchSelectList[index].value = e[e.length - 1];
    // },
    getTreeName(data, id, index) {
      data.forEach(item => {
        if (item.id === id) {
          this.detailListName[index] = item.name;
        } else {
          if (item.children) {
            this.getTreeName(item.children, id, index);
          }
        }
      });
    },
    cascaderChange(e, index) {
      const ids = JSON.parse(JSON.stringify(e));
      const id = ids.pop();
      this.getTreeName(this.nowOption, id, index);
    },
    getValue2(e, index) {
      this.searchSelectList[index].value = e + '';
      let obj = {};
      obj = this.nowOption.find((item) => {
        return item.id === e;
      });
      this.detailListName[index] = obj.name;
    },
    // 获取班次数据
    async getShiftData() {
      try {
        const res = await configurationGetConfigurationByEnum({
          configurationTypeEnum: 'SHIFT_TIME'
        }, {});
        const data = JSON.parse(res.rows?.processConfiguration) || {};
        this.shiftList = Object.keys(data).map(key => ({
          name: key,
          value: data[key]
        }));
        console.log(this.shiftList);
        console.log(res);
      } catch (error) {}
    },
    minusSearch(index) {
      this.searchSelectList.splice(index, 1);
    },
    addSearch() {
      this.searchSelectList.push({ value: null });
    },
    async search() {
      try {
        this.loading = true;
        this.detailListx = [];
        setTimeout(() => {
          this.detailListx = JSON.parse(JSON.stringify(this.detailListName));
        }, 0);
        this.allData = [];
        const { dateType, dateVal2, dateVal, shiftVal, statisticsType } = this.searchData;
        // const res = await listAndonMttRMtbF({}, {
        //   startDate: dateType === 'month' ? dateVal.substring(0, 8) + '01' : dateVal.substring(0, 5) + '01-01',
        //   endDate: dateType === 'month' ? dateVal.substring(0, 8) + '31' : dateVal.substring(0, 5) + '12-31',
        //   shiftStart: shiftVal ? shiftVal.split('-')[0] : '',
        //   shiftEnd: shiftVal ? shiftVal.split('-')[1] : '',
        //   statisticsType,
        //   statisticsValues: this.searchSelectList.map(item => item.value[item.value.length - 1])
        // });
        console.log(statisticsType, shiftVal, dateVal);
        // eslint-disable-next-line camelcase
        // var g_dNum = this.searchSelectList.map(item => item.value[item.value.length - 1]);

        var valueArr = this.searchSelectList.map(item => {
          console.log((typeof item.value));
          // eslint-disable-next-line valid-typeof
          if ((typeof item.value) !== 'string') {
            return item.value[item.value.length - 1];
          } else {
            return item.value;
          }
        });

        var column = statisticsType === 1 ? 'groupId' : 'deviceCode';

        var condition = {
          conditionJson1: [
            {
              // column: statisticsType === 1 ? 'groupId' : 'deviceCode',
              column: column,
              operator: 1,
              ptype: 'int',
              type: 'in',
              value: valueArr.join(',')
            }
          ]
        };

        const res = await getDeviceExceptionComparedAnalyzeNew({
          dateTypeEnum: dateType === 'day' ? 'DAY' : 'MONTH',
          startTime: dateType === 'day' ? dateVal2[0] + ' 00:00:00' : dateVal2[0] + ' 00:00:00',
          endTime: dateType === 'day' ? dateVal2[1] + ' 00:00:00' : dateVal2[1] + ' 00:00:00',
          shiftStart: shiftVal ? shiftVal.split('-')[0] : '',
          shiftEnd: shiftVal ? shiftVal.split('-')[1] : ''
        }, condition);
        this.allData = res.rows || [];

        this.$nextTick(() => {
          this.$refs.detailTable.init();
          this.$refs.detailChart.init();
        });
        this.loading = false;
      } catch (error) {
        this.loading = false;
      }
    },
    reset() {
      this.detailListx = [];
      this.detailListName = [''];
      this.searchData.statisticsType = 1;
      this.searchData.dateVal = '';
      this.searchData.dateVal2 = [getStartTime(), getEndTime()];
      this.searchData.shiftVal = '';
      this.searchData.dateType = 'day';
      this.searchData.MTBFVal = '850';
      this.searchData.MTTRVal = '120';
      this.searchSelectList = [
        { value: '' }
      ];
      this.getGroupTree();
    },
    /**
     * @description: 获取类型树数据
     * @param obj ztree对象
     * @return: 无
     * @author: ZJM
     */
    async getGroupTree() {
      try {
        const res = await deviceGroupGetTreeFunc();
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.nowOption = res.rows;
          console.log('jiujiuwo', this.nowOption);
          // this.searchSelectList[0].value = [this.nowOption[0].children[0].children[0].id];
          var arr = [];
          if (this.nowOption[0]) {
            arr.push(this.nowOption[0].id);
            if (this.nowOption[0].children[0]) {
              arr.push(this.nowOption[0].children[0].id);
              // if (this.nowOption[0].children[0].children[0]) {
              //   arr.push(this.nowOption[0].children[0].children[0].id);
              // }
            }
          }
          this.searchSelectList[0].value = arr;
          // this.getTreeName(this.nowOption, 8, 0);
          this.search();
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    async getDev() {
      try {
        const res = await deviceGetPageDeviceComplexByConditionAction({
          current: 1,
          size: 99999999
        },
        {});
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.nowOption = res.rows.map((item) => {
            item.id = item.deviceCode;
            item.name = item.deviceName;
            return item;
          });
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    }
  },
  components: {
    // 加载未审核组件
    SearchBox,
    HeaderTitle,
    HeadOperationItem,
    detailTable,
    chartModule
  }
};
