import { showWarningMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
import {
  STATUS_CODE,
  TITLE,
  BTN_TEXT
} from '@/utils/constantsFile';
// 导入所需接口
// import { GetList, WarningStatistics } from '@/api/equipmentmonitoring';
import { getDeviceExceptionComparedAnalyzeNew } from '@/api/warn';
export default {
  components: {},
  props: {
    tableInfo: {
      type: Array,
      default: () => []
    },
    param: {
      type: Object
    }
  },
  data() {
    return {
      BTN_TEXT: BTN_TEXT,
      deviceName: '',
      lineOptions: [],
      groupOptions: [],
      // table是否加载
      tableLoadings: true,
      // table数据
      tableData: [],
      // table表头配置
      tableColumn: [],
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now();
        }
      }
    };
  },
  computed: {

  },
  watch: {
  },
  created() {
    // this.findList();
    this.init();
  },
  methods: {
    init() {
      console.log(this.tableInfo);
      console.log(this.param);
      const { dateType, dateVal, MTBFVal, MTTRVal } = this.param;
      // const unit = dateType === 'month' ? '月' : '日';
      console.log(dateVal);
      this.tableColumn = [{
        field: 'title',
        title: '',
        visible: true,
        width: '160',
        align: 'center',
        fixed: 'left'
      }];
      var clumns = [];
      if (dateType === 'month') {
        this.tableColumn[0].title = '月份';
        clumns = this.tableInfo.map((item, index) => ({
          field: item.reportStartTime.substring(0, 7),
          title: item.reportStartTime.substring(0, 7),
          width: '100',
          align: 'center'
        }));
      } else {
        this.tableColumn[0].title = '日期';
        clumns = this.tableInfo.map((item, index) => ({
          field: item.reportStartTime.substring(0, 11),
          title: item.reportStartTime.substring(0, 11),
          width: '100',
          align: 'center'
        }));
      }
      // clumns = this.tableInfo.map((item, index) => ({
      //   field: item.reportStartTime.substring(0, 11),
      //   title: item.reportStartTime.substring(0, 11) + unit,
      //   width: '100',
      //   align: 'center'
      // }));
      console.log(this.tableColumn, clumns);
      this.tableColumn = [...this.tableColumn, ...clumns];
      const tableData = [{
        title: 'MTBF(min)'
      },
      {
        title: 'MTTR(min)'
      },
      {
        title: 'MTBF标准线'
      },
      {
        title: 'MTTR标准线'
      }];
      if (dateType === 'month') {
        this.tableInfo.forEach((item, index) => {
          tableData[0][item.reportStartTime.substring(0, 7)] = item.mtbf.toFixed(2);
          tableData[1][item.reportStartTime.substring(0, 7)] = item.mttr.toFixed(2);
          tableData[2][item.reportStartTime.substring(0, 7)] = MTBFVal;
          tableData[3][item.reportStartTime.substring(0, 7)] = MTTRVal;
        });
      } else {
        this.tableInfo.forEach((item, index) => {
          tableData[0][item.reportStartTime.substring(0, 11)] = item.mtbf.toFixed(2);
          tableData[1][item.reportStartTime.substring(0, 11)] = item.mttr.toFixed(2);
          tableData[2][item.reportStartTime.substring(0, 11)] = MTBFVal;
          tableData[3][item.reportStartTime.substring(0, 11)] = MTTRVal;
        });
      }

      this.tableData = tableData;
      console.log(this.tableColumn);
    },
    /**
     * @description:获取表格数据
     * @param
     * @return:
     * @author: CH
     */
    async findList() {
      var searchContent = {
        // conditionJson1: [
        //   {
        //     column: 'startTime',
        //     value: this.startTime
        //   },
        //   {
        //     column: 'endTime',
        //     value: this.endTime
        //   }
        // ]
      };
      if (this.typeValue === 0) {
        searchContent.conditionJson1.push({
          column: 'groupId',
          operator: 1,
          ptype: 'int',
          type: 'eq',
          value: this.modelValue
        });
      } else {
        searchContent.conditionJson1.push({
          column: 'deviceCode',
          operator: 1,
          ptype: 'string',
          type: 'eq',
          value: this.modelValue
        });
      }
      var param = {
        dateTypeEnum: 'MONTH',
        startTime: this.startTime,
        endTime: this.endTime
      };

      try {
        const res = await getDeviceExceptionComparedAnalyzeNew(param, searchContent);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 将数据写入表单
          this.tableDatas = res.rows;

          var arr = res.rows;
          var arrJson = [{
            title: '故障时间'
          }, {
            title: '故障次数'
          }, {
            title: '运行时间'
          }, {
            title: 'MTBF'
          }, {
            title: 'MTTR'
          }];

          for (var i = 0; i < arr.length; i++) {
            this.tableColumns.push({
              field: arr[i].deviceCode + 'task',
              title: arr[i].deviceName,
              visible: true,
              minWidth: '200',
              align: 'center'
            });

            arrJson[0][arr[i].deviceCode + 'task'] = this.timeStampSeconds(arr[i].warnAllTime);
            arrJson[1][arr[i].deviceCode + 'task'] = arr[i].warnAllCount + ' 次';
            arrJson[2][arr[i].deviceCode + 'task'] = this.timeStampSeconds(arr[i].runTime);
            arrJson[3][arr[i].deviceCode + 'task'] = this.timeStampSeconds(arr[i].mtbf);
            arrJson[4][arr[i].deviceCode + 'task'] = this.timeStampSeconds(arr[i].mttr);
          }
          this.tableDatas = arrJson;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    exportTable() {
      // this.$refs.xGrid.exportData('MTTR&MTBF统计报表', this.tableDatas);
      this.$refs.xGrid.exportData({
        filename: 'MTTR&MTBF统计报表'
      });
    },
    timeStampSeconds(cellValue) {
      var day = parseInt(cellValue / 60 / 60 / 24);
      var hour = parseInt(cellValue / 60 / 60 % 24);
      var min = parseInt(cellValue / 60 % 60);
      var s = parseInt(cellValue % 60);
      cellValue = '';
      if (day > 0) {
        cellValue = day + '天';
      }
      if (hour > 0) {
        cellValue += hour + '小时';
      }
      if (min > 0) {
        cellValue += parseFloat(min) + '分钟';
      }
      if (s > 0) {
        cellValue += parseFloat(s) + '秒';
      }
      return cellValue;
    }
  }
};
